"use client";
import React, { useContext, useEffect, useState } from "react";
import { UserContext } from "@/context/user.context";
import { UserPen, LogOut } from "lucide-react";
import { useRouter } from "next/navigation";
import Link from "next/link";

const UserProfile: React.FC = () => {
  const { user, updateUser, signOut, cancelSubscription } =
    useContext(UserContext);
  const router = useRouter();
  const [formData, setFormData] = useState({
    email: "",
    company: "",
    password: "",
    telefono: "",
    username: "",
  });
  const [isEditing, setIsEditing] = useState(false);

  useEffect(() => {
    if (!user) {
      router?.push("/signin");
    } else {
      setFormData({
        username: user.username || "",
        email: user.email || "",
        telefono: user.telefono || "",
        company: user.company || "",
        password: "",
      });
    }
  }, [user, router]);

  if (!user) return null;

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    await updateUser({ ...formData, id: user.id });
    setIsEditing(false);
  };

  const handleSignOut = () => {
    signOut();
  };

  const toggleEditMode = () => {
    setIsEditing(!isEditing);
  };

  return (
    <section className="px-2 py-3 mx-auto max-w-7xl">
      <div className="relative w-[75vw] mx-auto overflow-hidden bg-gray-800 shadow-2xl sm:px-6 rounded-2xl flex flex-col">
        <div className="relative flex flex-wrap items-center justify-between gap-3 px-4 py-6 md:flex-row">
          <div className="flex items-center gap-3">
            <Link
              href="/"
              onClick={handleSignOut}
              className="transition-transform transform hover:scale-110"
            >
              <LogOut size={48} className="text-indigo-200/65" />
            </Link>
            <div className="flex flex-col">
              <h2 className="text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-indigo-400 via-purple-400 to-pink-400">
                {user.username}
              </h2>
              <p className="text-sm text-indigo-200/65">{user.id}</p>
            </div>
          </div>
          <p className="w-full text-2xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-indigo-400 via-purple-400 to-pink-400 md:text-right md:w-auto">
            {user.company}
          </p>
        </div>
      </div>

      {/* Sección de suscripción */}
      {user.subscripcion && (
        <div className="mt-8 bg-gray-800 shadow-2xl rounded-2xl p-6 w-[75vw] mx-auto">
          <h3 className="text-2xl font-semibold text-transparent bg-clip-text bg-gradient-to-r from-indigo-400 via-purple-400 to-pink-400">
            Suscripción
          </h3>
          <p className="mt-2 text-gray-300">
            Plan: {user.subscripcion.plan.nombre}
          </p>
          <p className="text-gray-300">Estado: {user.subscripcion.status}</p>
          <p className="text-gray-300">
            Último pago:{" "}
            {new Date(user.subscripcion.fechaUltimaPaga).toLocaleDateString()}
          </p>
          <p className="text-gray-300">
            Vence el:{" "}
            {new Date(user.subscripcion.fechaVencimiento).toLocaleDateString()}
          </p>
          {user.subscripcion.status !== "cancelled" && (
            <button
              onClick={() => cancelSubscription(user.subscripcion.id)}
              className="px-6 py-3 mt-4 font-semibold text-white transition-transform transform bg-red-600 rounded-lg shadow-md hover:scale-110 hover:bg-red-700"
            >
              Cancelar Suscripción
            </button>
          )}
        </div>
      )}
    </section>
  );
};

export default UserProfile;
